.product-list {
  display: flex;
  flex-flow: row wrap;
  gap: 50px 0;
  justify-content: space-between;
  margin-bottom: 50px;
  margin-top: 50px;
}

.product {
  align-items: center;
  border: solid gray 1px;
  display: flex;
  flex-direction: column;
  height: 350px;
  justify-content: center;
  padding: 10px;
  width: 275px;
}

.product img {
  width: 150px;
}
