.container {
  display: flex;
  justify-content: space-between;
}

.container-detalhe {
  background-color: rgb(241, 241, 241);
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin: 5vh;
  min-width: 150px;
  overflow-y: scroll;
  padding: 0 10px;
  width: 70vh;
}

.container-info {
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 40%;
}

.header {
  background-color: rgb(227, 227, 227);
  display: flex;
  height: 35px;
  justify-content: space-around;
  width: 214vh;
}

.foto {
  width: 150px;
}

.comentarios {
  background-color: rgb(227, 227, 227);
  border: 1px solid;
  text-align: center;
}

.comentarios-rate {
  display: flex;
  gap: 35px;
  justify-content: space-around;
}

textarea {
  height: 35px;
}

.forms {
  display: flex;
  flex-wrap: wrap;
  margin: 5vh;
  min-width: 150px;
  padding: 0 10px;
  width: 70vh;
}
