main {
  display: flex;
  flex-direction: row;
  width: 100%;
}

aside {
  background-color: rgb(241, 241, 241);
  display: flex;
  flex-direction: column;
  height: 90vh;
  margin: 5vh;
  min-width: 150px;
  overflow-y: scroll;
  padding: 0 10px;
  width: 300px;
}

.button-categories {
  background-color: rgb(241, 241, 241);
  border: none;
  padding: 10px;
}

.home-content {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 50px;
  width: 80%;
}

.cart-ancor {
  background-color: rgb(227, 227, 227);
  color: rgb(30, 30, 30);
  padding: 15px 45px;
  text-decoration: none;
}

.cart-ancor:hover {
  background-color: rgb(199, 196, 196);
  color: rgb(30, 30, 30);
  padding: 15px 45px;
  text-decoration: none;
}
